@import 'fonts';
@import 'var';

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.page {
  height: 100%;
  font-family: 'Roboto-Regular', 'Arial', serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 17px;
}

.page-body {
  min-width: 320px;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  margin: 0;
}

.container {
  padding: 0 20px;
}
/*640px*/
@media (min-width: 640px) {
  .container {
    padding: 0 30px;
  }
}
/*768px*/
@media (min-width: 768px) {
  .container {
    padding: 0 40px;
  }
}

/*1440px*/
@media (min-width: 1440px) {
  .container {
    padding: 0px 150px 0px 61px;
  }
}

.title {
  font-size: 24px;
  font-weight: 400;
  letter-spacing: 2px;
  color: $accent;
}